  <script>
  import axios from "axios";
  import { v4 as uuidv4 } from "uuid";
  import agreementContent from "@/assets/agreementContent.js";
  console.log("Imported Agreement Content:", agreementContent);
  export default {
    name: "PaymentPlans",
    data() {
      return {
        plans: [
          {
            id: 1,
            title: "超值专家面试包",
            tag: "两杯奶茶，解决一次面试",
            label: "专业可靠",
            price: 0.1,
            originalPrice: 139,
            baseDuration: "30分钟",
            bonusDuration: "15分钟",
            restriction: "此套餐无限制购买",
            tokenAmount: 20000,
          },
          {
            id: 2,
            title: "教练力荐面试包",
            tag: "一顿火锅，稳过一家公司",
            price: 199,
            label: "教练推荐",
            originalPrice: 399,
            baseDuration: "2小时",
            bonusDuration: "45分钟",
            restriction: "此套餐无限制购买",
            tokenAmount: 100000,
          },
          {
            id: 3,
            title: "导师操盘专属包",
            tag: "1-2节面试辅导课，陪伴整个求职周期",
            price: 799,
            label: "导师严选",
            originalPrice: 1999,
            baseDuration: "10+小时",
            restriction: "此套餐有限制购买",
            tokenAmount: 500000,
            menber:true
          },
          {
            id: 4,
            title: "应急标准包",
            tag: "两餐食堂，应对基础面试",
            price: 0.01,
  
            label: "流畅清晰",
  
            originalPrice: 88,
            baseDuration: "30分钟",
            bonusDuration: "10分钟",
            restriction: "此套餐有限制购买",
            tokenAmount: 500000,
          },
          {
            id: 5,
            title: "内网信息包",
            tag: "一顿外卖，检索全网信息",
            price: 59,
            label: "严谨全面",
  
            originalPrice: 148,
            baseDuration: "60分钟",
            bonusDuration: "30分钟",
            restriction: "此套餐有限制购买",
            tokenAmount: 500000,
          },
        ],
        showPaymentModal: false,
        selectedPlan: null,
        selectedPaymentMethod: null,
        agreementAccepted: false,
        showAgreementModal: false,
        agreementContent: agreementContent, // To hold the content of the agreement
        // showNotification: false,
        notificationMessage: "",
        // 月度会员部分
        isMenberWay:false,
        couponCode:""
      };
    },
    mounted() {
      console.log("Full URL:", window.location.href);
      const hashString = window.location.hash.slice(1); // 取得 hash 部分并去掉 #
      console.log("Hash String:", hashString);
      const hashParams = new URLSearchParams(hashString.split("?")[1]); // 取得 ? 后的部分进行解析
      console.log("HashParams Entries:");
      for (const [key, value] of hashParams.entries()) {
        console.log(`${key}: ${value}`);
      }
      const orderId = hashParams.get("order_id");
      const status = hashParams.get("status");
  
      console.log("Order ID:", orderId);
      console.log("Status:", status);
      if (orderId && status) {
        this.handlePaymentReturn(orderId, status);
      }
    },
    methods: {
      openPaymentModal(plan) {
        this.isMenberWay = !!plan.menber
        this.selectedPlan = plan;
        this.showPaymentModal = true;
        this.selectedPaymentMethod = null;
        this.agreementAccepted = false; // Reset agreement acceptance
      },
      closePaymentModal() {
        this.showPaymentModal = false;
        this.selectedPlan = null;
        this.selectedPaymentMethod = null;
        this.agreementAccepted = false;
      },
      selectPaymentMethod(method) {
        this.selectedPaymentMethod = method;
      },
      processPayment() {
        if (this.isMenberWay) {
          if(!this.couponCode){
            this.$message.error("月度会员需填写优惠卷");
            return;
          }
          if(this.couponCode != "666666"){
            this.$message.error("优惠卷不对");
            return;
          }

          
        }
        if (!this.agreementAccepted) {
          this.$message.error("请选择支付方式，且阅读并同意《会员服务协议》");
          return;
        }
  
        if (this.selectedPaymentMethod === "alipay") {
          this.initiatePayment(this.selectedPlan, "/api/pay/create");
        } else if (this.selectedPaymentMethod === "wechat") {
          this.initiatePayment(this.selectedPlan, "/api/wechatpay_create");
        } else {
          this.$message.error("请选择支付方式，且阅读并同意《会员服务协议》");
        }
      },
      showNotificationMessage(message) {
        this.notificationMessage = message;
        this.showNotification = true;
        setTimeout(() => {
          this.showNotification = false;
        }, 3000);
      },
      openAgreementModal() {
        console.log(this.agreementContent);
        this.showAgreementModal = true;
      },
      closeAgreementModal() {
        this.showAgreementModal = false;
      },
      // initiatePayment(plan) {
      //     const transactionId = uuidv4();
      //     const paymentData = {
      //         order_id: transactionId,
      //         amount: plan.price,
      //         subject: plan.title,
      //         username: localStorage.getItem('username')
      //     };
      //     console.log("PAY:", paymentData);
      //     axios.post('/api/pay/create', paymentData)
      //         .then(response => {
      //             console.log("1");
      //             console.log("Payment URL:", response.data.paymentUrl);
      //             window.location.href = response.data.paymentUrl;
      //             this.pollPaymentStatus(transactionId, plan); // 开始轮询支付状态
      //         })
      //         .catch(error => {
      //             console.error('初始化支付失败:', error);
      //         });
      // },
      initiatePayment(plan, apiEndpoint) {
        let transactionId = uuidv4().replace(/-/g, ""); // Removes dashes to make it shorter
        transactionId = transactionId.slice(0, 32); // Ensures it doesn't exceed 32 characters
        const paymentData = {
          order_id: transactionId,
          amount: plan.price,
          subject: plan.title,
          username: localStorage.getItem("username"),
        };
  
        axios
          .post(apiEndpoint, paymentData)
          .then((response) => {
            if (this.selectedPaymentMethod === "alipay") {
              window.location.href = response.data.paymentUrl; // Redirect for Alipay
              this.pollPaymentStatus(transactionId, plan, this.selectedPaymentMethod);
            } else if (this.selectedPaymentMethod === "wechat") {
              // Handle WeChat Pay by displaying the QR code
              this.showWeChatQRCode(response.data.codeUrl);
              this.pollPaymentStatus(transactionId, plan, this.selectedPaymentMethod); // Start polling payment status
            }
          })
          .catch((error) => {
            console.error("初始化支付失败:", error);
          });
      },
      showWeChatQRCode(codeUrl) {
        const qrCodeContainer = document.createElement("div");
        qrCodeContainer.className = "qr-code-container";
  
        const qrCodeImage = document.createElement("img");
        qrCodeImage.src = `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(
          codeUrl
        )}`;
        qrCodeImage.alt = "WeChat Pay QR Code";
  
        qrCodeContainer.appendChild(qrCodeImage);
        document.body.appendChild(qrCodeContainer);
  
        // Optionally, add a close button to remove the QR code 
        const closeBtn = document.createElement("button");
        closeBtn.textContent = "关闭";
        closeBtn.className = "close-qr-code";
        closeBtn.onclick = () => {
          document.body.removeChild(qrCodeContainer);
        };
        qrCodeContainer.appendChild(closeBtn);
      },
      pollPaymentStatus(transactionId, plan, selectedPaymentMethod) {
        console.log("Selected Payment Method:", selectedPaymentMethod);
        const username = localStorage.getItem("username");
        const interval = setInterval(() => {
        const endpoint = "/api/pay/query"
        //selectedPaymentMethod === "alipay"
            //? "/api/pay/query"
           // : "/api/wechatpay_query";
        axios.post(endpoint, { order_id: transactionId, username: username })
            .then((response) => {
                const status = response.data.trade_status;
                if (status === "TRADE_SUCCESS") {
                    clearInterval(interval);
                    this.refreshAccountStatus();
                    this.$set(plan, "paymentStatus", "支付成功");
                    this.showNotification("支付成功！感谢您的购买。", "success");
                } else if (status === "TRADE_FAIL") {
                    clearInterval(interval);
                    this.$set(plan, "paymentStatus", "支付失败");
                    this.showNotification("支付失败，请检查您的支付信息。", "error");
                }
            })
            .catch((error) => {
                console.error("支付状态更新失败:", error);
                clearInterval(interval);
                this.$set(plan, "paymentStatus", "支付状态获取失败");
                this.showNotification(
                    "支付状态获取失败，请检查网络连接或稍后重试。",
                    "error"
                );
            });
    }, 2000); // 每 2 秒轮询一次
      },
      handlePaymentReturn(orderId, status) {
        console.log(orderId);
        console.log(status);
        const plan = this.plans;
        if (status === "success") {
          this.pollPaymentStatus(orderId, plan); // 调用支付状态轮询函数
        } else if (status === "failed") {
          this.$set(plan, "paymentStatus", "支付失败");
          this.showNotification("支付失败，请检查您的支付信息。", "error");
        }
      },
      refreshAccountStatus() {
        console.log("支付成功，正在刷新账户状态...");
        // 更新账户状态的逻辑
      },
      showNotification(message, type) {
        const notification = document.createElement("div");
        notification.className = `notification ${type}`;
        notification.textContent = message;
        document.body.appendChild(notification);
        setTimeout(() => {
          document.body.removeChild(notification);
        }, 5000);
      },
    },
  };
  </script>
